import { createSlice } from "@reduxjs/toolkit";
import { initialState } from "./initialState";
export const slice = createSlice({
    name: "reducer",
    initialState: initialState,
    reducers: {
        addNote(state, action) {
            state.notes.push(action.payload);
        },
        editNote(state, action) {
            state.notes = state.notes.filter((note) => {
                if (note.id === action.payload.id)
                    note.title = action.payload.title;
                return note;
            });
        },
        removeNote(state, action) {
            state.notes = state.notes.filter((note) => note.id !== action.payload.id);
        },
        addTags(state, action) {
            state.tags.push(...action.payload);
        },
        removeUnusedTags(state) {
            state.tags = state.tags
                .map((tag) => {
                if (state.notesTags.find((noteTag) => noteTag.tagId === tag.id))
                    return tag;
            })
                .filter(Boolean);
        },
        addNotesTags(state, action) {
            state.notesTags.push(...action.payload);
        },
        removeNotesTags(state, action) {
            state.notesTags = state.notesTags.filter((noteTag) => noteTag.noteId !== action.payload.id);
        },
    },
});
const { actions, reducer } = slice;
export const { addNote, editNote, removeNote, addTags, removeUnusedTags, addNotesTags, removeNotesTags, } = actions;
export default reducer;
